export default {
  name: '',
  components: {},
  data () {
    return {
      isInputFocused: false,
      inputValue: ''
    }
  },
  props: [''],
  computed: {
    cancelStyle () {
      if (this.inputValue) {
        return 'inline-block'
      } else {
        return 'none'
      }
    },
  },
  methods: {
    onInputBlur () {
      this.isInputFocused = false
      this.inputValue = ''
    },
    clearSearch () {
      this.isResultShow = false
      this.isInputFocused = true
      this.inputValue = '' 
    },
  }
}



