import Vote from './vote/index.vue'
export default {
  components: {
    Vote,
  },
  data() {
    return {
      isRulesShow: false
    };
  },
  methods: {
    showRules () {
      document.body.style.position = 'fixed'
      this.isRulesShow=true
    },
    hideRules () {
      document.body.style.position = ''
      this.isRulesShow=false
    }
  }
};



